/* ******************** Fonts ******************** */
@font-face {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 500;
  src: url('./fonts/Ubuntu-Bold.woff2') format('woff2');
}  
@font-face {
  font-family: 'Ubuntu-Bold';
  font-style: normal;
  font-weight: 900;
  src: url('./fonts/Ubuntu-Regular.woff2') format('woff2');
}  
:root {
  --main-top: 0rem;
  --main-bottom: 0rem;

  --color1: #204b5e;
  --color2: #426b65;
  --color3: #baab6a;
  --color4: #fbea80;
  --color5: #fdfac7;

  --color-background: #ffffff;
  --color-text: #000000;

  --font-family: 'Ubuntu';
  --font-family-bold: 'Ubuntu-Bold';
}
*,
*::before,
*::after {
    box-sizing: border-box;
}
html {
  margin: 0;
  font-size: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  width: 100%;
  min-height: 100vh;
  font-family: var(--font-family);
  font-size: clamp(1rem, calc(1rem + 0.5vw), 1.25rem);
  line-height: 1.5;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;

  background-color: var(--color-background);
  color: var(--color-text);
}
code {
  font-family: var(--font-family);
}
a {
  transition: all 0.3s ease-out;
  text-decoration: none;
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; 
} 
a:focus {
  outline: none !important;
  box-shadow: none !important;
}
a:hover {
  color: var(--color2) !important;
}
img,
picture,
video,
canvas,
svg {
  max-width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  margin-bottom: 10px;
  font-family: var(--font-family);
  line-height: 1.2;
  overflow-wrap: break-word;
}
h1 {
  font-size: 34px;
} 
h2 {
  font-size: 30px;
} 
h3 {
  font-size: 24px;
} 
h4 {
  font-size: 20px;
} 
h5 {
  font-size: 18px;
} 
h6,
small {
  font-size: 14px;
}
p {
  font-family: var(--font-family);
  font-size: 16px;
  overflow-wrap: break-word;
  padding-bottom: 6px;
} 
ul {
  list-style: none;
  margin-bottom: 0;
}
nav {
  display: block; 
}
nav ul {
  list-style: none; 
}
main {
  margin-top: var(--main-top);
  margin-bottom: calc(2 * var(--main-bottom));
}
span {
  color: var(--color3);
}
strong {
  font-family: var(--font-family-bold);
  font-weight: 600;
}
.highlight {
  font-family: var(--font-family-bold);
  font-weight: 800;
  font-style: italic;
  color: var(--color4) !important;
}
