header {
    height: auto;
    background-color: #496489;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
    color: #212529;
    width: auto;
    padding: 10px;
}
.header-div {
    color: #fff;
    display: flex;
    align-items: center;
    width: auto;
    margin: 0 auto;
    padding: 18px 20px;
}
header .title {
    font-size: 30px;
    margin: 0;
}
header .title a {
    text-decoration: none;
    color: #fff;
}
.navbar {
    display: flex;
    justify-content: flex-end;
}
.nav-list {
    list-style-type: none;
    display: flex;
    padding: 0;
    margin: 0;
}
.nav-item {
    margin-left: 25px;
}
.nav-item:hover {
    color: #fff;
    box-shadow: inset 0 -1px 0 #fff;
}
.nav-item a {
    color: #fff;
    text-decoration: none;
}
@media (max-width: 768px) {
    header {
        width: auto;
        padding: 10px;
    }
    .header-div {
        flex-direction: column;
        width: auto;
        max-width: none !important;
    }
    .navbar {
        justify-content: center;
        margin-top: 10px;
    }
    .nav-item {
        margin: 0 10px;
    }
}